import React, { Component } from 'react';
import {Switch,Route, Link} from 'react-router-dom';
import Navbar from './Components/Navbar';
import Productlist from './Components/Productlist';
import Details from './Components/Details';
import PageNotFound from './Components/PageNotFound';
import Checkout from './Components/Checkout';
import Contacts from './Components/Contacts';
import Applelist from './Components/Applelist.js';
import Androidlist from './Components/Androidlist.js';
import Modal from './Components/Modal/Modal';

class App extends Component {
  state={
    showmodal: false,
    numberi:0
  }

  toShow =(i)=>{
    this.setState({showmodal: true})
    console.log('test',i)
    this.setState({numberi: i})
  }
   toNotShow =(i)=>{
    this.setState({showmodal: false})
  }
  render() {
    return (
      <div className="App">
       <Navbar /> 
       <Switch>
       <Route path='/'    exact component={Productlist}/>
       <Route path='/details'    exact component={Details}/>
       <Route path='/checkout'    exact component={Checkout}/>
       <Route path='/contacts'    exact component={Contacts}/>
       <Route  path='/details'    exact component={Details}/>
       <Route   path='/applelist' render={props => <Applelist toShow = {this.toShow}/> } />
      <Route   path='/androidlist' render={props => <Androidlist toShow = {this.toShow}/> } />
      
      

       
       </Switch>


        <Modal  passStateStatus={this.state.showmodal} close={this.toNotShow} passi={this.state.numberi}/>
      </div>
    );
  }
}

export default App;