import React from 'react';
import './Links.css';
import { Link} from 'react-router-dom';
import Logo from '.././images/logo.png';
function Links(props) {
	let passState = props.passState;    
	let classChange = 'Links';
	if (passState === false) {         
		classChange = 'Linksclose'
	}
	
		
  return (
 <div className='nav-main-container'>
    <div className={classChange}>
               <div className='nav-links'> 
                    <div className='logo-container'>
                        <img className='logo-image' src= {Logo} alt="logo"/>
                    </div>
                 <ul className='nav-ul'>
                    <li > <Link to='/' className='nav-li-a'> Products </Link> </li> 
                    <li > <Link to='/contacts' className='nav-li-a' > Contacts </Link> </li>
                 </ul>
                  <div className='checkout-box'>
                         <Link className='checkout-item' to='/checkout' > Checkout </Link>
                  </div>
               </div>
               
    </div>
  </div>
  );
}

export default Links;
