import React from 'react';
import Applecover from './images/coverapple.jpg';
import'./Apple.css';
import { Link} from 'react-router-dom';
const Apple = (props) => {
	return(
<div className='Apple-main-container'>
<h2> Aplle Phones </h2>
<img className='Apple-image' src= {Applecover} alt="foto colage"/>
<Link to='/applelist' className='Apple-button' > Products </Link>
</div>
);
}

export default Apple;
