import React from 'react';
import PhoneCard from './PhoneCard';
import {shopandroid} from './shoplist';
import './Applelist.css'


const Androidlist = (props) => {
	const androidlist = shopandroid.map((usher,i)=>{
		return <PhoneCard click={props.toShow.bind(this, shopandroid[i].id)} key={shopandroid[i].id} name={shopandroid[i].name} description={shopandroid[i].description}  price={shopandroid[i].price} photo={shopandroid[i].photo}/>
	})
	return(
<div className='applelist-main'>

<h2> Android Phones </h2>
<div className='applelist-list' >
{androidlist}
</div>
</div>
);
}

export default Androidlist;