import React from 'react';
import PhoneCard from './PhoneCard';
import {shopapple} from './shoplist';
import './Applelist.css'


const Applelist = (props) => {
	const applelist = shopapple.map((usher,i)=>{
		return <PhoneCard click={props.toShow.bind(this, shopapple[i].id)} key={shopapple[i].id} name={shopapple[i].name} description={shopapple[i].description} 
		price={shopapple[i].price} photo={shopapple[i].photo} />
	})
	return(
<div className='applelist-main'>
<h2 > Apple Phones </h2>
<div className='applelist-list' >
{applelist}
</div>
</div>
);
}

export default Applelist;