import React from 'react';
import Androidcover from './images/coverandroid.jpg';
import'./Apple.css';
import { Link} from 'react-router-dom';

const Android = (props) => {
	return(
<div className='Apple-main-container'>
<h2> Android Phones </h2>
<img className='Apple-image' src= {Androidcover} alt="foto colage"/>
<Link to='/androidlist' className='Apple-button' > Products </Link>
</div>
);
}

export default Android;