import React from 'react';
import'./Modal.css';
import {shopandroid, shopapple} from '.././shoplist';

const Modal = (props) => {
  let i = props.passi
  let shop = shopandroid.concat(shopapple)
console.log('list:', shop)
	if (props.passStateStatus===false){
		return null;

	}
	return(
<div className='main-modal'>
<p className='main-modal-title'> {shop[i].name}</p>
<img className='card-img-modal' src={shop[i].photo}/>
<p className='main-modal-description'> {shop[i].description} </p>
<p className='main-modal-description' > {shop[i].price} Euro</p>
<button onClick={props.close}  className='main-modal-button' > CLOSE </button>
</div>
);
}

export default Modal;